import { useEffect, useState } from 'react';
import { fetchLiveStream } from './apis/holoDev';
import res from './response';

function App() {
  const [liveStream, setLiveStream] = useState(null);

  useEffect(() => {
    // fetchLiveStream().then((res) => setLiveStream(res.data.lives));
    // fetchLiveStream().then((res) => setLiveStream(console.log(res)));
    setLiveStream(res.data.lives);
  }, []);

  const renderLiveStream = () => {
    if (!liveStream) return 'there is no live stream now....';

    return liveStream.map((stream) => {
      return (
        <div key={stream.id} className="w-full bg-gray-400">
          <div className=" p-10 w-full bg-yellow-600">
            <img
              className=" object-contain"
              src={stream.cover}
              alt={stream.title}
            />
          </div>
        </div>
      );
    });
  };

  return (
    <div className="text-center text-2xl text-green-900">
      Holo Viewer :)
      <div className="grid grid-cols-5 gap-1">{renderLiveStream()}</div>
      <div class="pt-6 ...">pt-6</div>
      <div class="mt-8 ...">mt-8</div>
    </div>
  );
}

export default App;
